import React, { type PropsWithChildren, type ReactNode } from 'react';
import { styled } from 'styled-components';

const StyledRoot = styled.div`
  position: relative;
  overflow: hidden;
  margin: 25px 0 40px;
  border-radius: 4px;
  background: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.1) 0 1px 3px 0;
  border: 1px solid hsla(203, 50%, 30%, 0.15);

  & .ardocs-picture {
    overflow: hidden;
    position: relative;

    & > div {
      display: block;
      position: relative;
      flex-wrap: wrap;
      overflow: auto;
      flex-direction: column;
      padding: 30px 20px;
      margin: -10px;
    }
  }
`;

export interface PictureBoxProps extends PropsWithChildren {
  children: ReactNode;
}

function PictureBox(props: PictureBoxProps): JSX.Element {
  const { children } = props;
  return (
    <StyledRoot className='ardocs ardoc-picture-box'>
      <div className='ardocs-picture'>
        <div>{children}</div>
      </div>
    </StyledRoot>
  );
}

export default PictureBox;
PictureBox.displayName = 'ArthurCanvas';
